<template>
  <div class="header">
    <div class="container">
      <h1 class="logo"><router-link to="/"></router-link></h1>
      <ul class="navs">
        <li class="home"><router-link to="/">首页</router-link></li>
        <li v-for="item in cateList" :key="item.id">
          <router-link :to="`/category/${item.id}`">{{
            item.name
          }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cateList: [],
    };
  },
  methods: {
    async getCateList() {
      const { data } = await this.$axios.get("/home/category/head");
      this.cateList = data.result;
    },
  },
  created() {
    this.getCateList();
  },
};
</script>

<style lang="less" scoped>
.header {
  background: #ffffff;
  .container {
    display: flex;
    align-items: center;
    height: 120px;
    .logo {
      margin-right: 20px;
      a {
        display: block;
        height: 70px;
        width: 260px;
        background: url(@/assets/images/logo.png) no-repeat center 0px / contain;
      }
    }
    .navs {
      width: 820px;
      display: flex;
      justify-content: space-around;
      li {
        a {
          display: inline-block;
          height: 32px;
          font-size: 16px;
          line-height: 32px;
          text-align: center;
          &:hover {
            color: #409eff;
          }
          &.router-link-exact-active {
            color: #409eff;
            border-bottom: 2px solid #409eff;
          }
        }
      }
    }
  }
}
</style>